import { Formik } from 'formik';
import {
  Wrapper,
  Dialog,
  KindStyle,
  SubBTN
} from './SignUp.styled';

const SignUp = ({ Close, kind_trainee, day, time }) => {
  const TimeOfDay = () => {
    if (time === "0800") {
      return (
        <>
          <KindStyle>8:00</KindStyle>
        </>
      )
    };
    if (time === "0900") {
      return (
        <>
          <KindStyle>9:00</KindStyle>
        </>
      )
    };
    if (time === "1000") {
      return (
        <>
          <KindStyle>10:00</KindStyle>
        </>
      )
    };
    if (time === "1100") {
      return (
        <>
          <KindStyle>11:00</KindStyle>
        </>
      )
    };
    if (time === "1200") {
      return (
        <>
          <KindStyle>12:00</KindStyle>
        </>
      )
    };
    if (time === "1300") {
      return (
        <>
          <KindStyle>13:00</KindStyle>
        </>
      )
    };
    if (time === "1400") {
      return (
        <>
          <KindStyle>14:00</KindStyle>
        </>
      )
    };
    if (time === "1500") {
      return (
        <>
          <KindStyle>15:00</KindStyle>
        </>
      )
    };
    if (time === "1600") {
      return (
        <>
          <KindStyle>16:00</KindStyle>
        </>
      )
    };
    if (time === "1700") {
      return (
        <>
          <KindStyle>17:00</KindStyle>
        </>
      )
    };
    if (time === "1800") {
      return (
        <>
          <KindStyle>18:00</KindStyle>
        </>
      )
    };
    if (time === "1900") {
      return (
        <>
          <KindStyle>19:00</KindStyle>
        </>
      )
    };
    if (time === "2000") {
      return (
        <>
          <KindStyle>20:00</KindStyle>
        </>
      )
    };
    return (
      <>
        <h3>    </h3>
      </>
    );
  }

  const DayOfWeek = () => {
    if (day === "monday") {
      return (
        <>
          <TimeOfDay />
          <KindStyle>  </KindStyle>
        </>
      )
    };
    if (day === "tuesday") {
      return (
        <>
          <TimeOfDay />
          <KindStyle>  </KindStyle>
        </>
      )
    };
    if (day === "wednesday") {
      return (
        <>
          <TimeOfDay />
          <KindStyle>  </KindStyle>
        </>
      )
    };
    if (day === "thursday") {
      return (
        <>
          <TimeOfDay />
          <KindStyle>  </KindStyle>
        </>
      )
    };
    if (day === "friday") {
      return (
        <>
          <TimeOfDay />
          <KindStyle>  '</KindStyle>
        </>
      )
    };
    if (day === "saturday") {
      return (
        <>
          <TimeOfDay />
          <KindStyle>  </KindStyle>
        </>
      )
    };
    if (day === "sunday") {
      return (
        <>
          <TimeOfDay />
          <KindStyle>  </KindStyle>
        </>
      )
    }
    return (
      <>
        <h3>     </h3>
      </>
    );
  };

  const CheckKindTrainee = () => {
    if (kind_trainee === "-") {
      return (
        <>
          <Dialog>
            <h3>,           .  ,   </h3>
            <button type="button" onClick={Close}>
              
            </button>
          </Dialog>
        </>
      )
    }
    return (
      <>
        <Dialog>
          <h4>  <KindStyle>{kind_trainee}  </KindStyle><DayOfWeek /></h4><br />
          <h4> ,   </h4><br />
          <Formik
            initialValues={{
              id: "",
              day: day,
              time: time,
              kind_trainee: kind_trainee,
              name: "",
            }}
            onSubmit={async values => {
              // await clientAPI.sendData(values);
              await new Promise(resolve => setTimeout(resolve, 500));
              alert(JSON.stringify(values, null, 2));
            }}
          >
            {props => {
              const {
                values,
                isSubmitting,
                handleChange,
                handleBlur,
                handleSubmit,
              } = props;
              return (
                <form
                  onSubmit={handleSubmit}
                >
                  <label htmlFor="name">
                     '
                  </label><br/>
                  <input
                    id="name"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.value}
                  /><br/>
                  <label htmlFor="name">
                      
                  </label><br/>
                  <input
                    id="id"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.value}
                  /><br/>
                  <SubBTN type="button" onClick={Close}>
                    
                  </SubBTN>
                  <SubBTN type="submit" disabled={isSubmitting}>
                    
                  </SubBTN>
                </form>
              );
            }}
          </Formik>
        </Dialog>
      </>
    )
  }

  return (
    <Wrapper>
      <CheckKindTrainee/>
    </Wrapper>
  )
};

export default SignUp;